/*-----------------------------------------------------------
全体スタイル
-----------------------------------------------------------*/
body {
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  font-size: 15px;
  font-weight: 400;
  letter-spacing: 0.1em;
  line-height: 2;
  position: relative;
  overflow-x: hidden;
  color: $base-font;
  background: $base-bg;
  width: 100%;
  font-family: "noto sans JP", sans-serif;
  &.open {
    height: 100vh;
  }
  @include mid {
    font-size: 14px;
  }
}
img {
  vertical-align: top;
}
.lista {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}
p {
  color: $base-font;
  text-align: left;
}

// サイズ
.f18 {
  font-size: clamp(15px, 1.8vw, 18px);
  text-align: left;
  width: min(95%, 860px);
}

/*リンク表示方法*/
a {
  text-decoration: none;
}
a:link {
  color: $base-font;
}
a:visited {
  color: $base-font;
}
a:active {
  color: $base-font;
}
a:hover {
  opacity: 0.7;
  img {
    opacity: 1;
  }
}
:has(> .lista) {
  position: relative;
}
.lista {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}
a[href*="tel:"] {
  pointer-events: none;
  cursor: default;
  text-decoration: none;
}
.flex {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.grid {
  display: grid;
  grid-template-columns: center;
}
.i-text {
  display: inline-block;
}
.ver-rl {
  writing-mode: vertical-rl;
}
.active1200 {
  display: none;
  @media screen and (max-width: 1200px) {
    display: block;
  }
}
.active1000 {
  display: none;
  @include thou {
    display: block;
  }
}
.active768 {
  display: none;
  @include ipad {
    display: block;
  }
}
.active591 {
  display: none;
  @include mid {
    display: block;
  }
}
.active414 {
  display: none;
  @include sp {
    display: block;
  }
}
.vanish1200 {
  @include thou2 {
    display: none;
  }
}
.vanish1000 {
  @include thou {
    display: none;
  }
}
.vanish768 {
  @include ipad {
    display: none;
  }
}
.vanish591 {
  @include mid {
    display: none;
  }
}
.vanish414 {
  @include sp {
    display: none;
  }
}
/*-----------------------------------------------------------
PCサイズ
-----------------------------------------------------------*/
.pc,
.pc2 {
  display: block;
  @include thou {
    display: none;
  }
}
.sp,
.sp2,
.spTel {
  display: none;
  @include thou {
    display: block;
  }
}
.pcTel {
  display: inline;
}

/*-----------------------------------------------------------
フォント
-----------------------------------------------------------*/
.mainTit {
  font-size: clamp(15px, 2vw, 20px);
  text-align: center;
  line-height: 1.3;
  font-weight: 500;
  letter-spacing: 0.1em;
  font-style: normal;
  font-family: "Shippori Mincho B1", "游明朝体", "Yu Mincho", YuMincho,
    "ヒラギノ明朝 Pro", "Hiragino Mincho Pro", serif;
}
.mainTit .en {
  font-size: clamp(30px, 6vw, 60px);
  display: block;
  font-family: "Cinzel", serif;
  font-weight: 300;
  font-style: normal;
  margin-bottom: 12px;
  letter-spacing: 0.06em;
}
.noto {
  font-family: "Noto Sans JP", sans-serif;
  font-style: normal;
  font-weight: 500;
}
.mincho {
  font-family: "Shippori Mincho B1", "游明朝体", "Yu Mincho", YuMincho,
    "ヒラギノ明朝 Pro", "Hiragino Mincho Pro", serif;
  font-style: normal;
  font-weight: 500;
}
.en {
  font-family: "Cinzel", serif;
  font-style: normal;
  font-weight: 300;
  letter-spacing: 0.06em;
}
.come {
  margin: 8px auto;
  text-align: left;
  width: 100%;
  color: $base-font;
}

/*-----------------------------------------------------------
テーブル
-----------------------------------------------------------*/
table {
  border-collapse: separate;
  @include ipad {
    width: 100%;
    th,
    td {
      width: 100% !important;
      display: block;
    }
  }
}

/*-----------------------------------------------------------
モーダル
-----------------------------------------------------------*/
#modal {
  display: none;
}

/*-----------------------------------------------------------
ページトップ
-----------------------------------------------------------*/
#pageTop {
  position: fixed;
  bottom: 245px;
  right: 25px;
  z-index: 9999;
  background: #1a1a1a;
  width: 60px;
  height: 60px;
  border-radius: 3px;
  a {
    width: 100%;
    height: 100%;
    display: grid;
    place-content: center;
    transition: 0.2s;
    &:hover {
      background: #000;
    }
  }
  @include mid {
    display: none !important;
  }
}

/*-----------------------------------------------------------
スクロールバー
-----------------------------------------------------------*/
.scroll-pane {
  width: 100% !important;
  height: 300px; /*箱の高さ*/
  margin: 40px auto 0;
  overflow-y: auto;
  &:focus {
    outline: none;
  }
}
